.about {
  background-color: transparent;
  font-size: 1.1em;
  font-family: "Helvetica";
}

.profile {
  max-height: 60vh;
}

.my-details {
  background-image: linear-gradient(to right, #f5f7fa, #c3cfe2);
}

@media screen and (max-width: 990px) {
  .profile {
    margin-left: 7vw;
    max-height: 40vh;
  }
  .my-details {
    max-width: 90vw;
    margin: auto;
  }
}
